/*==========>>  2.5.     Preloader   <<==========*/
#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, #4E024E 0%, #4543D2 59%, #4441E2 100%);
  z-index: 100500;
}
#page-preloader .preloader {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 80px;
  height: 80px;
  display: inline-block;
  border-radius: 50%;
  perspective: 780px;
  transform: translate(-50%, -50%);
}
.preload_inner {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.preload_inner.preload-one {
  left: 0%;
  top: 0%;
  animation: preload-rotate-one 1.15s linear infinite;
  border-bottom: 4px solid #6e48ce;
}
.preload_inner.preload-two {
  right: 0%;
  top: 0%;
  animation: preload-rotate-two 1.15s linear infinite;
  border-right: 4px solid #d85068;
}
.preload_inner.preload-three {
  right: 0%;
  bottom: 0%;
  animation: preload-rotate-three 1.15s linear infinite;
  border-top: 4px solid #4ba3d7;
}
@keyframes preload-rotate-one {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}
@keyframes preload-rotate-two {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}
@keyframes preload-rotate-three {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}
